import Vue from 'vue'
import Router from 'vue-router'
import Manual from './views/Manual.vue'
import Auto from './views/Auto.vue'
import Settings from './views/Settings.vue'
import Nvue from './views/Nvue.vue'
import Logs from './views/Logs.vue'
import Logsbkp from './views/Logsbkp.vue'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'manual',
      component: Manual
    },
    {
      path: '/settings',
      name: 'settings',
      component: Settings
    },
    {
      path: '/auto',
      name: 'auto',
      component: Auto
    },
    {
      path: '/nvue',
      name: 'nvue',
      component: Nvue
    },
    {
      path: '/logs',
      name: 'logs',
      component: Logs
    },
    {
      path: '/logsbkp',
      name: 'logsbkp',
      component: Logsbkp
    }
  ]
})
