<template>
  <v-container grid-list-md mt-2 pa-0 style="height:520px" overflow-hidden>
    <v-layout row wrap>
      <v-flex white elevation-1 md12 mx-4 mt-3 pb-3 style="height:50px">
        <v-card-title dark color="primary">
          <div>
            <h3 class="red--text title">Manual Mode</h3>
          </div>
        </v-card-title>
      </v-flex>
      <v-layout row wrap mx-4 mb-4 pa-0>
        <v-flex
          elevation-1
          mx-n2
          white
          md4
          pt-3
          style="height:300px"
          v-for="gauges in gauges"
          :key="gauges.name"
        >
          <v-layout justify-center mt-4>
            <vue-ellipse-progress
              class="v-label headline"
              :progress="gauges.progress"
              :thickness="16"
              :color="gauges.color"
              :angle="0"
              line="butt"
              :half="true"
              :size="240"
              lineMode="out -4"
              emptyColor="#B0BEC5"
              :legend-value="gauges.show"
            >
              <span slot="legend-value" class="v-label headline">/{{gauges.rangeHigh}}</span>
              <p slot="legend-caption" class="title mt-2 ma-0">{{gauges.name}}</p>
              <span
                slot="legend-caption"
                style="font-size:13pt"
                class="mt-0 pa-0 theme--light"
                v-html="gauges.unit"
              >{{gauges.unit}}</span>
            </vue-ellipse-progress>
          </v-layout>
        </v-flex>
      </v-layout>

      <v-flex elevation-2 white md12 pa-0 mb-1 mx-4 style="height:110px">
        <v-layout row wrap>
          <v-flex md6 offset-md3 mt-3 white>
            <h3 class="red--text title pl-3">Fan Speed: {{speed}}%</h3>
            <v-card color="white" flat>
              <v-slider
                v-model="speed"
                :disabled="switch1"
                :color="color"
                step="5"
                label="100"
                inverse-label
                class="px-3"
              >
                <template v-slot:prepend>
                  <div class="v-label theme--light pt-1">0</div>
                </template>
              </v-slider>
            </v-card>
          </v-flex>
          <v-flex md1 red mt-1 pa-0 fill-height>
            <v-card white flat>
              <v-icon v-if="switch1===true" color="red" class="pt-2" large>lock</v-icon>
              <v-icon v-else color="gray" class="pt-2" large>lock_open</v-icon>
              <v-switch color="red" class="pt-0" v-model="switch1"></v-switch>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    switch1: true,
    speed: 30,
    interval: null,
    dialog: false,

    gauges: [
      {
        name: "Airflow",
        progress: 20.6,
        show: 2.6,
        unit: "(000's M<sup>3</sup>/Hr)",
        color: "green",
        rangeHigh: 10
      },
      {
        name: "Enclosure Pressure",
        progress: 37,
        show: 8.9,
        unit: "(Pa)",
        color: "orange",
        rangeHigh: 24
      },
      {
        name: "HEPA Blockage",
        progress: 80,
        show: 80,
        unit: "(Percent)",
        color: "red",
        rangeHigh: 100
      }
    ]
  }),

  computed: {
    color() {
      if (this.speed < 60) return "red";
      if (this.speed < 80) return "red";
      if (this.speed < 100) return "red";
      return "red";
    },
    animationDuration() {
      return `${this.speed}s`;
    }
  },
  methods: {
    decrement() {
      this.speed--;
    },
    increment() {
      this.speed++;
    }
  }
};
</script>